import React, { useState, useEffect } from "react";
import { db } from "./firebase";
import Task from "./Task";
import "../Style/Tasks.css";
import FlipMove from "react-flip-move";

function ParagraphWrapper() {
  const [tasks, setTasks] = useState([]);

  /*useEffect(() => {
    db.collection("tasks")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          console.log(`${doc.id} => ${doc.data().Task}`);
        });
      });
  }, []);
  */
  //Pobieranie el z bazy
  useEffect(() => {
    let unsunscribe;
    unsunscribe = db
      .collection("tasks")
      .orderBy("timestamp", "desc")
      .onSnapshot((snapshot) => {
        setTasks(
          snapshot.docs.map((curr) => ({
            task: curr.data().Task,
            id: curr.id,
          }))
        );
      });
    return () => {
      unsunscribe();
    };
  }, []);

  return (
    <div className="wrapper">
      <FlipMove>
        {tasks.map(({ id, task }) => (
          <Task key={id} task={task} id={id} />
        ))}
      </FlipMove>
    </div>
  );
}

export default ParagraphWrapper;
